import React, { Component } from 'react';

import './Piece.css';

class Piece extends Component {
  decorations() {
    var classNames = "Piece-" + this.props.value;
    if (this.props.value === 1) {
      console.log("Row: " + this.props.row + " col: " + this.props.column + " lastMove: " + this.props.lastMove);
    }
    if (this.props.lastMove && this.props.row === this.props.lastMove[0] && this.props.column == this.props.lastMove[1]) {
      classNames += " Piece-latest";
    }
    return classNames;
  }

  render() {
    return (
      <div className={"Piece " + this.decorations()}/>
    );
  }
}

export default Piece;


