import React, { Component } from 'react';

import Spinner from "./Spinner";

import './Sidebar.css';

class Sidebar extends Component {
  render() {
    return (
      <div className="Sidebar">
        {this.props.name}
        <Spinner active={this.props.active} />
      </div>
    );
  }
}

export default Sidebar;


